body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol,
ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.game-info {
  margin: 0px;
  text-align: center;
  font-size: 16px;
  width: 150px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 72px;
  font-weight: bold;
  line-height: 34px;
  height: 150px;
  width: 150px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
}

@media (max-width: 500px) {
  .square {
    height: 125px;
    width: 125px;
    font-size: 64px;
  }
}

@media (max-width: 420px) {
  .square {
    height: 115px;
    width: 115px;
    font-size: 64px;
  }
}

@media (max-width: 360px) {
  .square {
    height: 90px;
    width: 90px;
    font-size: 60px;
  }
}

@media (max-width: 320px) {
  .square {
    height: 75px;
    width: 75px;
    font-size: 48px;
  }
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.button-format {
  background-color: white;
  padding: 16px;
  margin: 5px;
  border-radius: 12px;
  border: 1px solid #999;
}
.button:focus {
  outline: none;
}
